import { useEffect, useState } from 'react';
import './App.css';
import Loader from './components/loader';
import Showcase from'./components/showcase';

function App() {
  const [loader,setLoader] = useState(true);
  useEffect(()=> {
    setTimeout(() => {
      setLoader(false);
    },5000);
  })
  return (
    <>
    {loader ? (<Loader/>) : (<Showcase />)}
    </>
  );
}

export default App;
