import React from 'react';
import {motion} from 'framer-motion';
import './index.css'

const container={
    show: {
        transition: {
            staggerChildren: 0.5,
        },
    },
}

const item = {
    hidden: { opacity: 0, y: 500 },
    show: {
      opacity: 1,
      y: 0,
      transition: {
        ease: [0.6, 0.01, -0.05, 0.95],
        duration: 1.6,
      },
    },
    exit: {
      opacity: 0,
      y: -200,
      transition: {
        ease: "easeInOut",
        duration: 0.8,
      },
    },
  };

function index(props) {
    return (
        <motion.div className="image-container"
            variants={container}
            initial="hidden"
            animate="show"
            exit="exit"
        >
            <motion.img variants={item} className="image-item1"  src="assets/img4.webp" alt="logo"/>
            <motion.img variants={item} className="image-item2"  src="assets/img4.webp" alt="logo"/>
            <motion.img variants={item} className="image-item3"  src="assets/img4.webp" alt="logo"/>
            <motion.img variants={item} className="image-item4"  src="assets/img4.webp" alt="logo"/>
            <motion.img variants={item} className="image-item5"  src="assets/img4.webp" alt="logo"/>
            <motion.img variants={item} className="image-item6"  src="assets/img4.webp" alt="logo"/>
        </motion.div>
    );
}

export default index;